td {
  color: cadetblue;
  border-bottom: 1px solid lightcoral;
  margin: 5px;
  text-align: center;
}

table {
  margin: 0 auto;
}

.button {
  padding: 12px 5px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  font-weight: bold;
  background: crimson;
  line-height: 19px;
  text-transform: uppercase;
  color: #fff;
  font-size: 19px;
  border: none;
  margin: 200px auto;
}

.button:hover {
  cursor: pointer;
}

.button_search {
  border-radius: 5px;
  border: 2px solid cadetblue;
  padding: 8px 5px;
}

.button_remove {
  border: 2px solid crimson;
  border-radius: 5px;
  padding: 5px;
  background: white;
  margin: 0 10px;
}